<template>
  <div class="container">
    <div class="img-wrapper" v-bind:class="{fullScreen: fullScreen}" v-lazy-container="{ selector: 'img' }">
      <img v-bind:data-src="src" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'Pane',
  props: {
    src: String,
    maxWidth: {
      type: String,
      default: "100%",
    },
    maxHeight: String,
  },
  data: function(){
    return {
      fullScreen: false,
    }
  },
  computed: {
    styleObject(){
      return this.$mq !== 'sm' ? this.fullScreen === true ?
      {
        "max-height": "80vh",
      } : {
        "width": this.maxWidth,
      } : {
        "max-width": "100%",
      }
    },
  },
  methods: {
    toggleFullScreen: function(){
      this.fullScreen = !this.fullScreen
      this.$emit('click', this.fullScreen)
    },
  },
}
</script>

<style scoped>
  .img-wrapper {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1em;
    width: 60%;
  }
  .fullScreen {
    background: white;
    height: 100%;
    width: 100%;
    padding-top: 1em;
    position: fixed;
    z-index: 1000;
  }
  .container {
    transition: opacity 0.8s;
    overflow: hidden;
  }
  img {
    transition: opacity 0.8s;
    opacity: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
  img[lazy=loading] {

  }
  img[lazy=loaded] {
    opacity: 1;
  }
</style>
