<template>
  <div class="content">
    <div class="filters">
      <ul>
        <li><span class="filter hvr-underline-from-center" v-bind:class="{active: filter === 'all'}" v-on:click="setFilter('all')">Everything</span></li>
        <li><span class="filter hvr-underline-from-center" v-bind:class="{active: filter === 'people'}" v-on:click="setFilter('people')">People</span></li>
        <li><span class="filter hvr-underline-from-center" v-bind:class="{active: filter === 'places'}" v-on:click="setFilter('places')">Places</span></li>
        <li><span class="filter hvr-underline-from-center" v-bind:class="{active: filter === 'artsy'}" v-on:click="setFilter('artsy')">Artsy</span></li>
        <li><span class="filter hvr-underline-from-center" v-bind:class="{active: filter === 'event'}" v-on:click="setFilter('event')">Event</span></li>
      </ul>
    </div>
    <div class="grid-mode">
      <ul>
        <li><span class="grid-switch hvr-grow" v-bind:class="{active: gridMode === 'flow'}" v-on:click="setGrid('flow')"><i class="fas fa-square"></i></span></li>
        <li><span class="grid-switch hvr-grow" v-bind:class="{active: gridMode === 'grid'}" v-on:click="setGrid('grid')"><i class="fas fa-th-large"></i></span></li>
      </ul>
    </div>
    <h3>Pages still under construction</h3>
    <div class="gallery-container">
      <div class="gallery" name="gallery">
        <component class="gallery-media" v-for="m in filteredMedia" v-bind:key="m.name" v-bind:is="getComponent(m.component)" v-bind="m.props" v-bind:style="styleObject" @click="expandMedia">
        </component>
      </div>
    </div>
    <div class="divider"></div>
  </div>
</template>

<script>
//Might be better to cache loaded images with a display none instead of discarding the card
import Pane from './Pane.vue'
import Group from './Group.vue'
import galleryItems from '../gallery.json'

let componentMap = {
  "Pane": Pane,
  "Group": Group,
}

let gridItemDim = {
  "sm": "50%",
  "md": "33%",
  "lg": "25%",
}

//Maybe separate component for multiple photos grouped together ('group')
//Component should therefore always be 100% width for flow and grid width for grid
//Group component takes a list of srcs with lazy container and also layout pattern (2x2, etc)
//Current way is messy and browser behavior is wackycalc

export default {
  name: 'Photography',
  props: {
    media: {
      type: Array,
      default: function(){
        return galleryItems
      }
    },
  },
  computed: {
    filteredMedia: function() {
      return this.media.filter((m)=>(new Set(m.tag)).has(this.filter) || this.filter === 'all')
    },
    styleObject: function() {
      return this.gridMode === "grid" ? {
        width: gridItemDim[this.$mq],
        height: gridItemDim[this.$mq],
        padding: "0",
        overflow: "hidden",
      } : {
         width: "100%"
      }
    },
  },
  data: function(){
    return {
      filter: 'all',
      gridMode: 'flow',
      prevGridMode: 'flow',
    }
  },
  methods: {
    setFilter: function(f) {
      this.filter = f
      this.$nextTick(this.$Lazyload.lazyLoadHandler)
    },
    setGrid: function(g) {
      this.gridMode = g
      this.prevGridMode = this.gridMode
      this.$nextTick(this.$Lazyload.lazyLoadHandler)
    },
    getComponent: function(compString) {
      return componentMap[compString]
    },
    expandMedia: function(fs) {
      this.gridMode = fs ? 'flow' : this.prevGridMode
    }
  },
}
</script>

