<template>
  <div v-bind:class="{fullScreen: fullScreen}" v-bind:style="blockStyleObject">

    <div class="container" v-lazy-container="{ selector: 'img' }" v-bind:style="styleObject" @click="toggleFullScreen">
      <img v-bind:data-src="src" />
    </div>

  </div>
</template>

<script>
export default {
  name: 'Group',
  props: {
    src: Array,
    col:  Number,
    row: Number,
  },
  data: function(){
    return {
      fullScreen: false,
    }
  },
  computed: {
    blockStyleObject(){
      return Boolean(this.block) === true ? {width: "100%"} : {}
    },
    styleObject(){
      return this.$mq !== 'sm' ? this.fullScreen === true ?
      {
        height: "80vh",
      } : {
        width: this.maxWidth,
        height: this.maxHeight,
        padding: "0 "+this.padding,
      } : {
        width: "100%",
      }
    },
  },
  methods: {
    toggleFullScreen: function(){
      this.fullScreen = !this.fullScreen
      this.$emit('click', this.fullScreen)
    },
  },
}
</script>

<style scoped>
  .fullScreen {
    background: white;
    height: 100%;
    width: 100%;
    padding-top: 1em;
    position: fixed;
    z-index: 1000;
  }
  img {
    transition: opacity 0.8s;
    object-fit:cover;
    opacity: 0;
  }
  img[lazy=loading] {

  }
  img[lazy=loaded] {
    opacity: 1;
  }
</style>
